import React,{useState} from 'react'
import "./util.css";
import "./main.css";
import Axios from 'axios';
const Contact = () => {
    const url = "http://localhost:8088/email-service/send-mail";
    const [response, setResponse] = useState({
        data : ""
    })
    const [data, setData] = useState({
        first_name : "",
        last_name  : "",
        email : "",
        phone : "",
        message : ""
    })
    function submit(e) {
        e.preventDefault();
        Axios.post(url,{
            first_name: data.first_name,
            last_name: data.last_name,
            mail: data.email,
            phone: data.phone,
            message: data.message
        })
        .then(res => {
            setResponse(res);
            console.log(res.data);
        })
    }
    function handle(e) {
        const newData = {...data};
        newData[e.target.id] = e.target.value;
        setData(newData);
        console.log(newData);
    }
    return (
        <div class="container-contact100">
            <div class="wrap-contact100">
                <form class="contact100-form validate-form" onSubmit={(e) => submit(e)}>
                    <span class="txt3">
                        {response.data}
                    </span>
                    <span class="contact100-form-title">
                        Contact Us
                    </span>
                    <label class="label-input100" for="first-name">Tell us your name *</label>
                    <div class="wrap-input100 rs1-wrap-input100 validate-input" data-validate="Type first name">
                        <input onChange={(e) => handle(e)} id="first_name" value={data.first_name} class="input100" type="text" name="first-name" placeholder="First name" />
                        <span class="focus-input100"></span>
                    </div>
                    <div  class="wrap-input100 rs2-wrap-input100 validate-input" data-validate="Type last name">
                        <input onChange={(e) => handle(e)} id="last_name" value={data.last_name} class="input100" type="text" name="last-name" placeholder="Last name" />
                        <span class="focus-input100"></span>
                    </div>
                    <label class="label-input100" for="email">Enter your email *</label>
                    <div class="wrap-input100 validate-input" data-validate="Valid email is required: ex@abc.xyz">
                        <input onChange={(e) => handle(e)} id="email" value={data.email} class="input100" type="text" name="email" placeholder="Eg. example@email.com" />
                        <span class="focus-input100"></span>
                    </div>
                    <label class="label-input100" for="phone">Enter phone number</label>
                    <div class="wrap-input100">
                        <input onChange={(e) => handle(e)} id="phone" value={data.phone} class="input100" type="text" name="phone" placeholder="Eg. +1 800 000000" />
                        <span class="focus-input100"></span>
                    </div>
                    <label class="label-input100" for="message">Message *</label>
                    <div class="wrap-input100 validate-input" data-validate="Message is required">
                        <textarea onChange={(e) => handle(e)} id="message" value={data.message} class="input100" name="message" placeholder="Write us a message"></textarea>
                        <span class="focus-input100"></span>
                    </div>
                    <div class="container-contact100-form-btn">
                        <button class="contact100-form-btn">
                            Send Message
                        </button>
                    </div>
                </form>
                <div class="contact100-more flex-col-c-m">
                    <div class="flex-w size1 p-b-47">
                        <div class="txt1 p-r-25">
                            <span class="lnr lnr-map-marker"></span>
                        </div>

                        <div class="flex-col size2">
                            <span class="txt1 p-b-20">
                                Address
                            </span>

                            <span class="txt2">
                            H. No. 8-10-267th New Housing Colony,, Near Ganesh
Maidan,, Bidar, Bidar, Karnataka, India, 585401.
                            </span>
                        </div>
                    </div>
                    <div class="dis-flex size1 p-b-47">
                        <div class="txt1 p-r-25">
                            <span class="lnr lnr-phone-handset"></span>
                        </div>

                        <div class="flex-col size2">
                            <span class="txt1 p-b-20">
                                Lets Talk
                            </span>

                            <span class="txt3">
                                +91 9731652247
                            </span>
                        </div>
                    </div>
                    <div class="dis-flex size1 p-b-47">
                        <div class="txt1 p-r-25">
                            <span class="lnr lnr-envelope"></span>
                        </div>

                        <div class="flex-col size2">
                            <span class="txt1 p-b-20">
                                General Support
                            </span>

                            <span class="txt3">
                                kskaranje@gmail.com
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Contact
